@import "../src/styles/scss/variables.scss";

.app {
  .toggleBars {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid $borderColor;
    position: absolute;
    top: 20px;
    width: 50px;
    height: 50px;
    visibility: hidden;
    cursor: pointer;
    @media (max-width: 1199px) {
      visibility: visible;
    }
  }
  .main {
    min-height: 100vh;
    padding-left: 260px;
    @media only screen and (max-width: 1199px) {
      padding-left: 0;
    }
  }
}
.toggleXmark {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid $borderColor;
  position: absolute;
  top: 20px;
  left: 259px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  visibility: visible;
  @media (max-width: 991px) {
    left: 249px;
  }
  @media (min-width: 1200px) {
    visibility: hidden;
  }
}
